/* eslint-disable react/prop-types */
/* eslint max-lines: ["error", 300] */
/* eslint-disable react-hooks/rules-of-hooks */
import React, { useCallback, useContext, useRef } from 'react';
import { DragDropContext, Droppable } from 'react-beautiful-dnd';
import { cx } from '@elliemae/ds-utilities/utils';
import GripperVertical from '@elliemae/ds-icons/GripperVertical';
import { mergeRefs } from '@elliemae/ds-utilities/system';
import FocusGroupContext from '@elliemae/ds-shared/FocusGroup/FocusGroupContext';
import { findItemById } from '../../tree/utils';
import { getDraggableProps } from '../utils';

const DroppableList = (renderers, instance) => ({ records, ...rest }) => {
  const {
    props: { expandTimerDelay = 1000, restrictDragAndDrop },
    actions: { setDraggedRecord, changeRecordsPosition, toggleExpand },
    state: { draggedRecord },
    itemRenderer,
  } = instance.getInstance();

  const focusContext = useContext(FocusGroupContext);

  const expandTimerRef = useRef();
  const draggedRecordShouldExpand = useRef(false);
  const currTargetRef = useRef({});

  const onDragUpdate = useCallback(
    (data) => {
      const { destination, combine } = data;
      clearTimeout(expandTimerRef.current);

      if (combine) {
        const { draggableId } = combine;
        const targetRecord = findItemById(records, draggableId);

        if (targetRecord && targetRecord.isGroup && !targetRecord.expanded) {
          expandTimerRef.current = setTimeout(
            () => toggleExpand(targetRecord),
            expandTimerDelay,
          );
        }
      }
      currTargetRef.current = destination || currTargetRef.current;
    },
    [records],
  );

  const onDragStart = useCallback(
    ({ source }) => {
      const draggingRecord = records[source.index];

      if (draggingRecord.expanded) {
        draggedRecordShouldExpand.current = true;
        toggleExpand(draggingRecord);
      }

      setDraggedRecord(draggingRecord);
    },
    [records],
  );

  const onDragEnd = useCallback(
    ({ destination, source, combine }) => {
      clearTimeout(expandTimerRef.current);
      const sourceIndex = source.index;
      const destinationIndex = destination && destination.index;

      if (sourceIndex === destinationIndex) return;

      const sourceRecord = records[sourceIndex];
      const targetRecord = destination && records[destinationIndex];
      const combineWith = combine && findItemById(records, combine.draggableId);

      const down = sourceIndex < destinationIndex;

      if (
        restrictDragAndDrop &&
        sourceRecord.parentNode?.id !== targetRecord.parentNode?.id
      )
        return;

      changeRecordsPosition({
        sourceRecord,
        targetRecord,
        combineWith,
        down,
      });

      if (draggedRecordShouldExpand.current) {
        draggedRecordShouldExpand.current = false;
        toggleExpand(sourceRecord);
      }

      currTargetRef.current = null;
      setDraggedRecord(null);

      if (focusContext)
        focusContext.focusItemByIndex((targetRecord || {}).index);
    },
    [records],
  );

  const { item: ItemComponent, list: ListComponent } = renderers;

  return (
    <DragDropContext
      onDragEnd={onDragEnd}
      onDragStart={onDragStart}
      onDragUpdate={onDragUpdate}
    >
      <Droppable
        droppableId="droppableId"
        isCombineEnabled
        mode="virtual"
        renderClone={(provided, snapshot, rubric) => {
          const targetRecord =
            (currTargetRef.current && records[currTargetRef.current.index]) ||
            {};
          const record = records[rubric.source.index];
          return (
            <ItemComponent
              record={record}
              {...getDraggableProps(
                {
                  className: 'item-cloned',
                },
                provided,
                snapshot,
              )}
            >
              <GripperVertical
                {...provided.dragHandleProps}
                className="drag-handle"
              />
              {itemRenderer({
                record,
                nestingLevel: targetRecord.nestingLevel,
              })}
            </ItemComponent>
          );
        }}
      >
        {(droppableProvided, snapshot) => {
          // Add an extra item to our list to make space for a dragging item
          // work in a virtual list
          const itemCount = snapshot.isUsingPlaceholder
            ? records.length + 1
            : records.length;

          return (
            <ListComponent
              {...rest}
              className={cx(rest.className, draggedRecord && 'dragging')}
              innerRef={mergeRefs(
                droppableProvided.innerRef,
                rest.innerRef,
                rest.ref,
              )}
              itemCount={itemCount}
              records={records}
            />
          );
        }}
      </Droppable>
    </DragDropContext>
  );
};

export default DroppableList;
